declare var require: any;
import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from "ngx-spinner";
import { DataServices } from '../core/services/data.service';
import { Router } from '@angular/router';
import { HttpHeaders } from '@angular/common/http';
import { NotificationService } from '../core/services/notification';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  userLogin: any = {};
  private result: any = {};
  constructor(private spinner: NgxSpinnerService,
    private dataservice: DataServices,
    private router: Router,
    private noti: NotificationService
  ) { }
  ngOnInit(): void {
    this.spinner.show();
    this.timeout();

  }
  private timeout() {
    setTimeout(() => {
      this.spinner.hide();
    }, 1500);
  }
  handleKeyboardEvent(event) {
    if (event.keyCode === 13) {
      this.login();
    }
  }
  public checkLogin() {
    if (localStorage.getItem('token') != null)
      this.router.navigate(['dashboard']);
  }
  // private getPermission(id) {
  //   this.dataservice.getPermission(id).subscribe((res) => {
  //     this.result = res;
  //     if (this.result.success === true) {
  //       this.spinner.hide();
  //       localStorage.setItem('permission', JSON.stringify(this.result.data));
  //       this.router.navigate(['dashboard']);
  //     }
  //   })
  // }
  public checkValid(): boolean {

    const regexCheck = /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/g;

    if (regexCheck.test(this.userLogin.username.trim())) {
      return true;
    }
    console.log(this.userLogin.username)
    this.noti.WarningNotification("Nhập đúng dạng email", 2000);
    return false;
  }
  login() {
    var hash = (this.userLogin.password);
    this.userLogin.password = hash;

    if (this.checkValid()) {
      this.spinner.show();
      this.dataservice.login(this.userLogin).subscribe(res => {
        this.spinner.hide();
        if (res.message === 'Logged in successfully') {
          if (res.data.role === "ADMIN"){
            this.noti.SuccessNotification("Đăng nhập thành công");
          localStorage.token = res.data.token;
          localStorage.setItem('user', JSON.stringify(res.data));
          // this.getPermission(localStorage.getItem('userid'));
          this.router.navigate(['dashboard']);
          }
          else{
            this.noti.ErrorNotification("Bạn không có quyền truy cập nhập thành công");
            return;
          }
          
        }
        if (res.success === false) {
          if (res.errors.code === '500') {
            this.spinner.hide();
            this.noti.ErrorNotification("Tài khoản của bạn chưa được kích hoạt");
            return;
          }
          this.spinner.hide();
          this.noti.ErrorNotification("Có lỗi xảy ra, vui lòng thử lại");
        }

      });
    }

  }
  public forgotpass() {
    this.router.navigate(['forgotpass']);
  }


}
